import React,{useState,useEffect}from 'react'
import { Link } from 'react-router-dom'
import {Form,Button,Row,Col} from 'react-bootstrap'
import { useDispatch,useSelector } from 'react-redux'
import Loader from '../components/Loader'
import Message from '../components/Message'
import FormContainer from '../components/FormContainer'
import { login } from '../actions/userActions'
function LoginScreen({location,history}) {//to destructure location
    //we set the two sets
    const [email,setEmail] = useState('')
    const [password,setPassword] = useState('')
    const dispatch = useDispatch()
    const redirect = location.search ? location.search.split('=')[1] :'/'

    const userLogin = useSelector(state => state.userLogin)
    //we want to destructure that
    const {error,loading,userInfo} = userLogin

    useEffect(() => {
        if(userInfo)
        {
            history.push(redirect)
        }
    },[history,userInfo,redirect])
    const submitHandler =(e) =>{
        e.preventDefault()
        //console.log('Submitted')
        dispatch(login(email,password))
    }
    return (
        <FormContainer>
            <h1>Sign In</h1>
            {error && <Message variant='danger'>{error}</Message>}
            {loading && <Loader/>}
            <Form onSubmit={submitHandler}>
                <Form.Group controlid='email'>
                    <Form.Label>Email Address</Form.Label>
                    <Form.Control
                    type='email'
                    placeholder='Enter Email'
                    value={email}
                    onChange={(e) => setEmail(e.target.value)} //on change we want to update our value
                                    //it going to be an error function, we take in the event
                    >

                    </Form.Control>

                </Form.Group>
                <Form.Group controlid='password'>
                    <Form.Label>Password</Form.Label>
                    <Form.Control
                    type='password'
                    placeholder='Enter Password'
                    value={password}
                    onChange={(e) => setPassword(e.target.value)} //on change we want to update our value
                                    //it going to be an error function, we take in the event
                    >

                    </Form.Control>
                </Form.Group>
                <Button type='submit' variant='primary'>
                    Sign In
                </Button>
            </Form>
            <Row className='py-3'>
                <Col>
                    New Customer?<Link 
                    to={redirect ? `/register?redirect=${redirect}` : '/register'}> 
                        Register
                    </Link>
                </Col>
            </Row>
        </FormContainer>
    )
}
export default LoginScreen